  src: url('@{fontsPrefix}plone-fontello.eot?52933264');
  src: url('@{fontsPrefix}plone-fontello.eot?52933264#iefix') format('embedded-opentype'),
       url('@{fontsPrefix}plone-fontello.woff?52933264') format('woff'),
       url('@{fontsPrefix}plone-fontello.ttf?52933264') format('truetype'),
       url('@{fontsPrefix}plone-fontello.svg?52933264#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
